.sidebar {
	flex: 0.3;
	background-color:  #131c21;
	border-top: 1px solid #49274b;
	max-width: 180px;
	color: white;
}

.sidebar__header {
display: flex;
border-bottom: 1px solid #49274b;
padding: 13px;
padding-bottom: 10px ;
}

.sidebar__info {
	flex: 1;
}

.sidebar__info > h2 {
	font-size: 15px;
	font-weight: 900;
	margin-bottom: 5px;
}

.sidebar__info > h3 {
	display: flex;
	align-items: center;
	font-size: 13px;
	font-weight: 400;
}

.sidebar__info > h3 > .MuiSvgIcon-root {
	font-size: 14px;
	margin-top: 1px;
	margin-right: 2px;
	color: green;
}

.sidebar__header > .MuiSvgIcon-root{
	padding: 8px;
	color: #49274b;
	font-size: 18px;
	background-color: white;
	border-radius: 999px;
}

.add {
	background-color: #ffa826;
	color: white;
}